import {ListItem, Breadcrumbs, ListItemText} from '@material-ui/core';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core'; 
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import CreateIcon from '@material-ui/icons/Create';

const useStyles = makeStyles({
  ikonit : {
      marginRight:5,
      fontSize : 20
  }
});

function Valikko() {
  const tyylit = useStyles();
    return (
      <Breadcrumbs>

        <ListItem button component={Link} to="/" >
            <FormatListBulletedIcon  className={tyylit.ikonit} />
          <ListItemText primary ="Aloitus"/>
        </ListItem>

        <ListItem button component={Link} to="/lisays"> 
                <CreateIcon className={tyylit.ikonit}/>
          <ListItemText primary ="Lisäys"/>
        </ListItem>
        
      </Breadcrumbs>
    )
}

export default Valikko;