import './App.css';
import { useState, useEffect } from 'react';
import { Container } from '@material-ui/core';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Aloitus from  './components/Aloitus';
import Valikko from  './components/Valikko';
import Lisays from  './components/Lisays';
import Poisto from  './components/Poisto';
import Muokkaus from  './components/Muokkaus';

function App() {

  const [linnut, setLinnut] = useState([]);
  
  linnut.sort(function(a,b){
    return b.aika-a.aika;
  })

  const tallennaLintulista = () =>{
      localStorage.setItem("Lintulista", JSON.stringify(linnut))
  }

  const avaaLintulista = () =>{
    if(localStorage.getItem("Lintulista")){
      setLinnut(JSON.parse(localStorage.getItem("Lintulista")));
    } else {
      setLinnut([]);
    }
  }

  useEffect(() =>{
    avaaLintulista();
  },[]); 

  useEffect(() =>{
    tallennaLintulista();
  },[linnut]); 

  return (
    <Container>
      <Router>
        <Valikko/>
        <Route path="/" exact>
          <Aloitus linnut={linnut} tallennaLintulista={tallennaLintulista}/>  
        </Route> 
        <Route path="/lisays">
          <Lisays linnut={linnut} setLinnut={setLinnut}/>  
        </Route> 

        <Route path="/poisto/:id">
          <Poisto linnut={linnut} setLinnut={setLinnut} />  
        </Route> 
        <Route path="/muokkaus/:id">
          <Muokkaus linnut={linnut} setLinnut={setLinnut} />  
        </Route> 
      </Router>
    </Container>   
  );
}

export default App;
