import { format } from 'date-fns';
import { IconButton, List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import Otsikko from "./Otsikko";
import { Link } from 'react-router-dom';
import DeleteIcon from '@material-ui/icons/Delete';
import BuildIcon from '@material-ui/icons/Build';

function Aloitus(props) {
    return (
        <>
            <Otsikko tyyli="iso">
                Kaikki bongaamasi linnut: 
            </Otsikko>

            <List style={{ width:320 }}>
                {props.linnut.map( (lintu, idx) => {
                return (
                    <ListItem key={idx}>
                    <ListItemText 
                        primary={`${lintu.laji},  ${lintu.paikka}`} 
                        secondary={format(lintu.aika, "d.M.y HH:mm")} 
                    />

                    <ListItemIcon>
                        <IconButton 
                          edge="end"
                          component={Link}
                          to={`/muokkaus/${lintu.id}`} 
                        >
                            <BuildIcon />
                        </IconButton>
                        </ListItemIcon>

                    <ListItemIcon>
                        <IconButton 
                          edge="end"
                          component={Link}
                          to={`/poisto/${lintu.id}`} 
                        >
                            <DeleteIcon />
                        </IconButton>
                        </ListItemIcon>
                    </ListItem>
                    )
                    })}
            </List>


        
        </>
    )
}

export default Aloitus;