import { useState} from 'react';
import { Button, Typography } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory, useParams } from 'react-router-dom';

function Poisto(props) {

    const history = useHistory();
    const { id } = useParams();


    var lintulaji = "";    

    try{    
        lintulaji =  props.linnut.filter((lintu) => {
            return (lintu.id === id);
        })[0].laji;
    }catch(error){ 
    
    }

    function kasitteleLomake(e) {

        e.preventDefault();
        let linnutApu = props.linnut.filter((lintu) => {
            return (lintu.id !== id);
        });
        props.setLinnut([...linnutApu]);
        history.push("/");
    }

    return (
        <form onSubmit={kasitteleLomake}>

        <Otsikko>Vahvista linnun poisto</Otsikko>

        <Typography
            style={{marginBottom : 10}}
        >Haluatko varmasti poistaa linnun {lintulaji} ?</Typography>

        <Button 
            type="submit"
            variant="contained"
            color="primary"
            style={{marginBottom : 10}}
        >Poista lintu</Button>
        <br/>
        <Button 
            component={Link}
            to="/"
            variant="contained"
            color="default"
        
        >Peruuta</Button>

        </form>
    )
}

export default Poisto;