import { format } from 'date-fns';
import { useRef, useState } from 'react';
import { Button, Typography, TextField } from '@material-ui/core';
import Otsikko from './Otsikko';
import { Link, useHistory, useParams } from 'react-router-dom';
import { MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';

function Muokkaus(props) {
   
    const [pvmaika, setPvmaika]=useState(new Date());
    const history = useHistory();
    const { id } = useParams();
    const laji = useRef();
    const paikkakunta = useRef();
    const [virheIlmoitus, setVirheilmoitus] = useState({});

    try{    
    var lintulaji =  props.linnut.filter((lintu) => {
        return (lintu.id === id);
    })[0].laji;

    var paikka =  props.linnut.filter((lintu) => {
        return (lintu.id === id);
    })[0].paikka;

    var aika =  props.linnut.filter((lintu) => {
        return (lintu.id === id);
    })[0].aika;

    }catch(error){
        var lintulaji = "";    
        var paikka = "";  
        var aika = new Date();
    }


    function kasitteleLomake(e) {
        e.preventDefault();
        let virheet = {};
        if (laji.current.value.length < 1) {
            virheet = { ...virheet, laji: "Anna laji." };
        }

        if (paikkakunta.current.value.length < 1) {
            virheet = { ...virheet, paikkakunta: "Anna paikka." };
        }
        if (pvmaika > new Date()) {
            virheet = { ...virheet, aika: "Et voi laittaa bongausaikaa tulevaisuuteen." };
        }

        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({ ...virheet });
        }
        else {
            let linnutApu = props.linnut.filter((lintu) => {
                return (lintu.id !== id);
            });

            let muokattuLintuApu = {
                id: id,
                laji: laji.current.value,
                aika: pvmaika.getTime(),
                paikka: paikkakunta.current.value
            };

            props.setLinnut([...linnutApu, muokattuLintuApu]);
            history.push("/");
            setVirheilmoitus({});
        }
    }

    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
            <form onSubmit={kasitteleLomake}>
                <Otsikko>
                    Muokkkaa havaintoa: <br/> 
                    {lintulaji} <br/>  
                    {paikka} <br/>  
                    {format(aika, "d.M.y HH:mm")}
                </Otsikko>

                <br/>
                <TextField 
                    label="Laji"
                    variant="outlined"
                    defaultValue={lintulaji}
                    error={Boolean(virheIlmoitus.laji)}
                    helperText={virheIlmoitus.laji}
                    inputRef={laji}
                    style={{marginBottom : 10}}
                />
                <br/>

                <TextField 
                    label="Paikka"
                    variant="outlined"
                    defaultValue={paikka}
                    error={Boolean(virheIlmoitus.paikkakunta)}
                    helperText={virheIlmoitus.paikkakunta}
                    inputRef={paikkakunta}
                    style={{marginBottom : 10}}
                />
                <br/>

                <DateTimePicker
                    label="Päivä ja aika"
                    inputVariant="outlined"
                    cancelLabel="Peruuta"
                    ampm={false}
                    value={pvmaika}
                    format="d.M.y HH:mm"
                    onChange={setPvmaika}
                    error={Boolean(virheIlmoitus.aika)}
                    helperText={virheIlmoitus.aika}
                    maxDate={new Date()}
                    style={{marginBottom : 10}}
                />
                <br/>
                <Button 
                    type="submit"
                    variant="contained"
                    color="primary"
                    style={{marginBottom : 10}}>
                    Tallenna muutokset
                </Button>
                    <br/>
                <Button 
                    component={Link}
                    to="/"
                    variant="contained"
                    color="default"        
                    >Peruuta
                </Button>
            </form>
        </MuiPickersUtilsProvider>
    )
}

export default Muokkaus;